// time
export const Util = {
    getNowDate() {
        let nowDateTime = new Date();
        return nowDateTime.getFullYear() + "-" + Util.formatTimeNumber(nowDateTime.getMonth() + 1) + "-" + Util.formatTimeNumber(nowDateTime.getDate());
    },
    getNowDay() {
        const dayStr = ['日', '一', '二', '三', '四', '五', '六'];
        let nowDateTime = new Date();
        return "星期" + dayStr[nowDateTime.getDay()];
    },
    getTimeHello() {
        let date = new Date();
        let hours = date.getHours();
        if (hours >= 4 && hours < 6) {
            return "天快亮了";
        }
        if (hours >= 6 && hours < 8) {
            return "早上好";
        }
        if (hours >= 8 && hours < 12) {
            return "上午好";
        }
        if (hours >= 12 && hours < 14) {
            return "中午好";
        }
        if (hours >= 14 && hours < 18) {
            return "下午好";
        }
        if (hours >= 18 && hours < 22) {
            return "晚上好";
        }
        if (hours >= 22 || hours < 4) {
            return "夜深了";
        }
    },
    formatTimeNumber(number) {
        return number >= 10 ? number : '0' + number;
    }
}